<template>
  <div id="app">
    <div class="wl-gantt-demo">
      <wlGantt
        ref="wl-gantt-demo"
        use-card
        use-real-time
        end-date="2021-9-01"
        start-date="2020-9-01"
        date-type="monthAndDay"
        :data="data"
        :columns="columns"
        :contextMenuOptions="contextMenuOptions"
        @selection-change="selectionChange"
        @expand-change="expandChange"
        @timeChange="timeChange"
        @taskRemove="taskRemove"
        @preChange="preChange"
        @taskAdd="taskAdd"
      >
        <template slot="prv">
          <!-- <el-table-column type="selection" width="55" align="center"> </el-table-column> -->
          <el-table-column type="index" width="55" label=""> </el-table-column>
        </template>
        <template #info-card="{ row }">
          <ul>
            <li>
              <label for="name"></label><span id="name">{{ row.name }}</span>
            </li>
          </ul>
        </template>
      </wlGantt>
    </div>
  </div>
</template>

<script>
import wlGantt from "@/components/gantt/gantt";
// import wlGantt from "@/pages/wl-gantt";
import "@/assets/css/clear.css";

export default {
  name: "Gantt",
  data() {
    return {
      data: [
        {
          id: "1",
          pid: "0",
          name: "",
          startDate: "2020-09-07",
          realStartDate: "2020-09-10",
          endDate: "2020-10-31",
          realEndDate: "2020-10-19",
          children: [
            {
              id: "1-1",
              pid: "1",
              name: "",
              startDate: "2020-09-10",
              endDate: "2020-09-13",
            },
            {
              id: "1-2",
              pid: "1",
              name: "",
              startDate: "2020-09-10",
              endDate: "2020-09-13",
              children: [
                {
                  id: "1-1-1",
                  pid: "1-1",
                  name: "",
                  startDate: "2020-09-10",
                  endDate: "2020-09-13",
                },
                {
                  id: "1-1-2",
                  pid: "1-1",
                  name: "",
                  startDate: "2020-09-10",
                  endDate: "2020-09-13",
                },
                {
                  id: "1-1-3",
                  pid: "1-1",
                  name: "",
                  startDate: "2020-09-10",
                  endDate: "2020-09-13",
                },
                {
                  id: "1-1-4",
                  pid: "1-1",
                  name: "",
                  startDate: "2020-09-10",
                  endDate: "2020-09-13",
                }
              ]
            },
            {
              id: "1-3",
              pid: "1",
              name: "",
              startDate: "2020-09-10",
              endDate: "2020-09-13",
            },
            {
              id: "1-4",
              pid: "1",
              name: "",
              startDate: "2020-09-10",
              endDate: "2020-09-13",
            },
            {
              id: "1-5",
              pid: "1",
              name: "",
              startDate: "2020-09-17",
              endDate: "2020-09-22",
            },
            {
              id: "1-6",
              name: "",
              pid: "1",
              startDate: "2020-09-25",
              endDate: "2020-09-30",
            }
          ],
        },
        {
          id: "2",
          name: "",
          startDate: "2020-09-20",
          endDate: "2020-10-31",
        },
        {
          id: "3",
          name: "",
          startDate: "2020-09-20",
          endDate: "2020-10-31",
        },
        {
          id: "4",
          name: "",
          startDate: "2020-09-20",
          endDate: "2020-10-31",
        },
        {
          id: "5",
          name: "",
          startDate: "2020-09-20",
          endDate: "2020-10-31",
        }
      ], // 
      selected: [], // 
      contextMenuOptions: [
        { label: "", prop: "name" },
        { label: "", prop: "startDate" },
        { label: "", prop: "endDate" },
      ],
      columns: [{ type: "name", minWidth: 150, colType: "expand" }], // namestartDateendDatepreTaskcloumnstypeslotfalseslotprvdefault
    };
  },
  methods: {
    aa(row) {
      console.log(row, 99);
    },
    /**
     * 
     * row: Object c
     */
    timeChange(row) {
      console.log(":", row);
    },
    //
    /**
     * 
     * row: Object 
     * oldval: [String, Array] 
     * handle: Boolean 
     */
    preChange(row, oldval, handle) {
      console.log(":", row, oldval, handle);
    },
    // 
    expandChange(row, expanded) {
      console.log(":", row, expanded);
    },
    // 
    selectionChange(/* val */) {
      // console.log("", val);
    },
    // 
    taskRemove(item) {
      console.log("", item);
    },
    // 
    taskAdd(item) {
      console.log("", item);
      // 
     /*  this.$set(
        item,
        "children",
        item.children.concat([
          {
            pid: item.id,
            id: "###",
            name: "",
            startDate: "2020-10-11",
            endDate: "2020-10-19"
          }
        ])
      );*/
      this.$refs["wl-gantt-demo"].loadTreeAdd(item.id, [
        {
          pid: item.id,
          id: "###",
          name: "",
          startDate: "2020-10-11",
          endDate: "2020-10-19",
          children: []
        },
      ]);
    },
    // 
    lazyLoad(tree, treeNode, resolve) {
      setTimeout(() => {
        resolve([
          {
            id: "1-1-1",
            pid: tree.id,
            name: "",
            startDate: "2020-09-10",
            endDate: "2020-09-13",
          },
        ]);
      }, 1000);
    },
  },
  components: {
    wlGantt,
  },
};
</script>

<style lang="scss">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 10px 10px 0;
  width: 100%
}

.wl-gantt-demo {
  width: 100%;
}
</style>



